const state = {
  count: 0,
  userName: "",
  collapse: true,
  tagsList: [],
  metaTitle: "相册列表",
};

// 同步
const mutations = {
  increment(state) {
    state.count++;
  },
  collapse(state) {
    state.collapse = !state.collapse;
  },

  // 更改state中的属性，同步操作
  hadndleCollapse(state, data) {
    state.collapse = data;
  },
  delTagsItem(state, index) {
    state.tagsList.splice(index, 1);
  },
  setTagsItem(state, data) {
    if (!data) {
      return;
    }
    state.tagsList.push({
      name: data.name,
      title: data.meta.title,
      path: data.fullPath,
      close: data.meta.close,
    });
  },
  clearTags(state) {
    const curItem = state.tagsList.filter((item) => {
      return !item.close;
    });
    state.tagsList = curItem;
  },
  closeTagsOther(state, data) {
    state.tagsList = data;
  },
  closeCurrentTag(state, data) {
    for (let i = 0, len = state.tagsList.length; i < len; i++) {
      const item = state.tagsList[i];
      if (item.path === data.$route.fullPath) {
        if (i < len - 1) {
          data.$router.push(state.tagsList[i + 1].path);
        } else if (i > 0) {
          data.$router.push(state.tagsList[i - 1].path);
        } else {
          data.$router.push("/");
        }
        state.tagsList.splice(i, 1);
        break;
      }
    }
  },

  // 更新路由信息
  CAHNGE_META_INFO(state, metaInfo) {
    state.metaTitle = metaInfo;
  },
};

// 异步
const acions = {
  increment({ commit }) {
    commit("increment");
  },

  collapse(context) {
    context.commit("collapse");
  },
};

export default {
  namespaced: true,
  state: state,
  mutations: mutations,
  actions: acions,
};
